import { Grid,Typography,Link,Tooltip} from '@material-ui/core';
import Zoom from '@material-ui/core/Zoom';
import React from 'react';
import { useFooterStyles } from './Styles/styles';
import {socialMedia} from './Datas/getData';
import {Favorite} from '@material-ui/icons';

function Footer(props) {
        const classes = useFooterStyles();                              
        return (           
            <div className={classes.footerContainer} id="CONTACT">             
                <Grid container justifyContent="center" alignItems='center' spacing={3}>
                    {socialMedia.map((contact,index) => (
                        <Grid key={index} item>
                           <Tooltip TransitionComponent={Zoom} title={contact.link}><Link href={contact.link}> {contact.socialMedia}</Link></Tooltip>
                        </Grid>
                    ))}                          
                </Grid>
                <Grid container justifyContent="center" alignItems='center' spacing={3}>
                        <Grid item>
                            <Typography variant="body2" align='center' color='textPrimary'>
                                    Made with <Favorite fontSize="small" className={classes.heart} /> in India
                            </Typography>
                        </Grid>
                </Grid>
            </div>
        );
}
export default Footer;